import {Button, Caption, Cell, FixedLayout} from "@vkontakte/vkui";
import Icon20WorkOutline from "@vkontakte/icons/dist/20/work_outline";
import React from "react";
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon24Reorder from '@vkontakte/icons/dist/24/reorder';


const CartLine = ({ go, order }) => {
    return (
        <FixedLayout vertical={'top'}>
            <Cell
                onClick={() => go('cart')}
                className='cellCart'
                before={<Button onClick={() => go('cart')} before={<Icon20WorkOutline/>} size={'m'}>Корзина</Button>}
            >
                <Caption
                style={{color: 'antiquewhite'}}
                >
                    <span style={{float: 'left'}}>Товаров - {order.meta ? order.meta.count : 0}, <Icon24MoneyCircle height={17} width={17} style={{float: 'right', paddingTop: 3,paddingLeft: 3}}/> {order.meta ? order.meta.sum : 0}</span>
                </Caption>
            </Cell>
        </FixedLayout>
    );
}

export default CartLine;